import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText, Alert } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label, ListGroup, ListGroupItem, ListGroupItemHeading, ListGroupItemText } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap'
import { createPeerId, createFreeGGVRequest, loadAccountInfo, calcAccountId, purchaseResource } from '../js/gegeUtil.js'


const moment = require('moment')
const ShareManager = require('../js/share')
const get = require('simple-get')
const { shareResource } = require('../js/gegeUtil')

class ResourceShare extends Component {

  constructor(props) {
    super(props);
    let res = { tags: '', price: '', fee: '', deadline: '', owner: '', id: '' }
    this.state = { collapse: true, resourceList: [], resource: res, rsId: '' }
    this.state.shareMsg = '';
    this.share = new ShareManager(this.state);
    this.userSec = ''
    /*
    this.userSec = window.sessionStorage.getItem("userSec");
    if (!this.userSec) {
      this.userSec = window.localStorage.getItem("userSec");
    }
    */
    this.fee = 1;
    this.deadline = 5;
    this.price = 1;
    this.tags = ''
    this.resName = ''
    this.resId = ''
    this.infoInterval = null
    this.torrentList = []
  }

  cancelShare() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  doShare() {
    shareResource(this.resId, this.userSec, this.fee, this.deadline, this.price, this.tags, this.resName, (ok) => {
      console.log("shareResource = " + ok);
      if (!ok) {
        this.setState({ shareMsg: 'err. 出错了' })
      } else {
        this.setState({ shareMsg: 'success. 交易提交成功，等待进入区块链' })
      }
    })
  }

  componentDidMount() {
    this.share.init(this.onTorrentDone.bind(this),this.onTorrentStart.bind(this))
  }

  prettyBytes(num) {
    var exponent, unit, neg = num < 0, units = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
    if (neg) num = -num
    if (num < 1) return (neg ? '-' : '') + num + ' B'
    exponent = Math.min(Math.floor(Math.log(num) / Math.log(1000)), units.length - 1)
    num = Number((num / Math.pow(1000, exponent)).toFixed(2))
    unit = units[exponent]
    return (neg ? '-' : '') + num + ' ' + unit
  }

  updateTorrentStatus(){
    //console.log('size ' + this.torrentList.length)
    this.torrentList.map((torrent)=>{
      //uploadSpeed peers
     let peersSpan =  document.querySelector('#pid' + torrent.infoHash);
     let speedSpan =  document.querySelector('#sid' + torrent.infoHash);
     peersSpan.innerHTML = torrent.numPeers;
     speedSpan.innerHTML = this.prettyBytes(torrent.uploadSpeed) + '/s';
    })
  }

  onTorrentStart(){
    this.setState({ shareMsg: 'working...处理中...' })
  }

  onTorrentDone(torrent) {
    if (torrent) {
      var info = 'Torrent info hash: ' + torrent.infoHash +
        'magnetURI=' + torrent.magnetURI +
        ' torrentFileBlobURL="' + torrent.torrentFileBlobURL + 'name=' + torrent.name;
      //console.log(info);
      this.resId = torrent.infoHash
      this.resName = torrent.name;
      var rlist = this.state.resourceList;
      rlist.push({id:this.resId,name : this.resName })
      this.setState({ resourceList: rlist})
      torrent.key = torrent.infoHash;
      //tags is not set yet
      //torrent.tags = this.tags;
      this.torrentList.unshift(torrent)
    }
    if(!this.infoInterval){
      this.infoInterval = setInterval(this.updateTorrentStatus.bind(this),3000);
    }
    this.setState({ shareMsg: '' })
  }

  updateSecret(e) {
    if (e && e.target) {
      this.userSec = e.target.value;
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
    }
  }

  updatePrice(e) {
    if (e && e.target) {
      this.price = e.target.value;
    }
  }

  updateTags(e) {
    if (e && e.target) {
      this.tags = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
    }
  }

  renderAddFile() {
    return (<Row>
      <Col xs="12">
        <Fade>
          <Card>
            <CardHeader>
              <i className="icon-share" onClick={this.toggle.bind(this)}>Share Resource to earn BTB(共享资源赚取BTB)</i>
              <div className="card-header-actions">
                <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle.bind(this)}><i className="icon-arrow-up"></i></Button>
                <Button color="link" className="card-header-action btn-close" onClick={this.toggleFade}><i className="icon-close"></i></Button>
              </div>
            </CardHeader>
            <Collapse isOpen={this.state.collapse} id="collapseExample">
              <CardBody>
                <FormGroup>
                  <Label htmlFor="appendedInputButtons">Account Secret(账户密码):</Label>
                  <div className="controls">
                    <InputGroup>
                      <Input id="appendedInputButtons" size="16" type="text" value={this.secret} onChange={this.updateSecret.bind(this)} placeholder='account secret 输入账户密码共享资源' />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>(发布者) Owner:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput" size="16" type="text" value={this.state.rsId} />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>Resource(资源):</InputGroupText>
                      </InputGroupAddon>
                      <input name="upload" type="file"></input>
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>Resource(资源ID) ID:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput1" size="16" type="text" value={this.resId } />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>Resource(资源标签) Tags:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput2" size="16" type="text" onChange={this.updateTags.bind(this)} placeholder='resource tags 资源描述' />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>Price(价格):</InputGroupText>
                      </InputGroupAddon>
                      <Input id="appendedPrependedInput3" size="16" type="text"  onChange={this.updatePrice.bind(this)} placeholder='resource price  资源价格,默认 1 个 BTB币' />
                      <InputGroupAddon addonType="append">
                        <InputGroupText>BTB</InputGroupText>
                      </InputGroupAddon>
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>Fee(矿工手续费):</InputGroupText>
                      </InputGroupAddon>
                      <Input id="appendedPrependedInput" size="16"  type="text" onChange={this.updateFee.bind(this)} placeholder='矿工手续费，最少1个BTB' />
                      <InputGroupAddon addonType="append">
                        <InputGroupText>BTB</InputGroupText>
                      </InputGroupAddon>
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>Deadline(有效期):</InputGroupText>
                      </InputGroupAddon>
                      <Input id="appendedPrependedInput" size="16"  onChange={this.updateDeadline.bind(this)} type="text" placeholder='交易有效期，默认5分钟' />
                      <InputGroupAddon addonType="append">
                        <InputGroupText>minutes(分钟)</InputGroupText>
                      </InputGroupAddon>
                    </InputGroup>
                  </div>
                </FormGroup>
                <div>
                  <p>{this.state.shareMsg}</p>
                </div>
                <div className="form-actions">
                  <Button type="submit" color="primary" onClick={(e) => this.doShare()}>Share(分享)</Button>
                  <Button color="secondary" onClick={(e) => this.cancelShare()}>Cancel(取消)</Button>
                </div>
              </CardBody>
            </Collapse>
          </Card>
        </Fade>
      </Col>
    </Row>)
  }


  renderResourceItems() {
    let items = this.state.resourceList.map((res) => {
      let pid = 'pid' + res.id;
      let sid = 'sid' + res.id;
      return (<ListGroupItem action>
        <ListGroupItemHeading>{res.id} : {res.name}</ListGroupItemHeading>
        <ListGroupItemText>
        <code> <span id={pid}></span> peers(节点)</code>. &#x2197;<code><span id={sid}></span>(上传速度)</code>
        </ListGroupItemText>
      </ListGroupItem>)
    })
    return items;
  }

  renderShareList() {
    return (<Row>
      <Col sm="12" xl="6">
        <Card>
          <CardHeader>
            <i className="fa fa-align-justify"></i><strong>Share List(我的资源列表)</strong>
            <Alert color="danger"><small>请不要刷新或关闭此页面，保持此页面打开状态下，购买过资源的用户才可以下载资源</small></Alert>
          </CardHeader>
          <CardBody>
            <ListGroup>
              {this.renderResourceItems()}
            </ListGroup>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  render() {
    return (
      <div className="animated fadeIn">
        {this.renderAddFile()}
        {this.renderShareList()}
      </div>

    );
  }
}

export default ResourceShare;



